body {
  background-color: #ddd
}
.icon {
  display: flex; /* 인라인 속성의 */
  align-items: center;
  width: 160px;
  height: 40px;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  cursor: pointer;
  margin: 20px;
  padding-left: 20px;
}

.icon svg {margin-right: 10px;}
.icon span{
  font-size: 17px;
}